/* 메뉴 펼쳐질 때 서서히 */
#page-top .collapsing {
  height: 0;
  overflow: hidden;
  transition: height 0.35s ease;
}
@media (prefers-reduced-motion: reduce) {
  #page-top .collapsing {
    transition: none;
  }
}
#page-top .collapsing.collapse-horizontal {
  width: 0;
  height: auto;
  transition: width 0.35s ease;
}
@media (prefers-reduced-motion: reduce) {
  #page-top .collapsing.collapse-horizontal {
    transition: none;
  }
}


