  box-sizing: border-box;
  position: absolute;
  width: 100%;
  padding: 0 35px;
  top: 50%;
  left: 0;
  -webkit-transform: translate3d(0, -50%, 35px) perspective(100px);
          transform: translate3d(0, -50%, 35px) perspective(100px);
  z-index: 20;
  display: block;
}

