/*바 2개 애니메이션*/
#banner font:before, #banner font:after {
    background: #fff;
    content: '';
    display: block;
    height: 3px;
    position: absolute;
    width: 100%;
}
#banner font:before{
    top:0;
    left:0;
    -webkit-animation-duration: 1.4s;
            animation-duration: 1.4s;
    -webkit-animation-name: slidein;
            animation-name: slidein;
}
#banner font:after {
    bottom:0;
    right :0;
    -webkit-animation-duration: 1.4s;
            animation-duration: 1.4s;
    -webkit-animation-name: slidein;
            animation-name: slidein;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
}
*, *:before, *:after {
    box-sizing: inherit;
}
.slidein {
  -webkit-animation-duration: 1.5s;
  animation-duration: 1.5s;
  -webkit-animation-name: slidein;
  animation-name: slidein;
  -webkit-animation-iteration-count: 2;
  animation-iteration-count: 2;
  -webkit-animation-direction: alternate;
  animation-direction: alternate;
}

@-webkit-keyframes slidein {
  0% {
      display: none;
      opacity: 0;
      width:0%;
  }
    30%{
        display: -webkit-flex;
        display: flex;
        opacity: 0;
        width:0%;
    }
  100% {
      opacity: 1;
   width:100%;
 }
}

@keyframes slidein {
  0% {
      display: none;
      opacity: 0;
      width:0%
  }
  30%{
    display: -webkit-flex;
    display: flex;
    opacity: 0;
     width:0%;
  }
  100% {
      opacity: 1;
      width:100%;
 }
}
