    font-size:17px;
}

.JobPostingModalDiv .modal button {
    outline: none;
    cursor: pointer;
    border: 0;
}
.JobPostingModalDiv .modal > section {
    width: 90%;
    height: 90%;

    max-width: 900px;

    margin:0 auto;
    border-radius: .3rem;
    //border: 1px solid rgba(0, 0, 0, 0.6);  /*모달 팝업창 테두리*/
