    font-weight: 700;
    text-align: center;
    color: #999;
    background-color: white;
    //background-color: transparent;
}
.JobPostingModalDiv .modal > section > main {
    //padding: 16px;
    border-bottom: 1px solid #dee2e6;
    border-top: 1px solid #dee2e6;
    text-align:left;
    font-size:14px;
    height: 92%;
}
.JobPostingModalDiv .modal > section > footer {
    //align-content: center;
    padding: 6px 17px;
    //text-align: right;
}
.JobPostingModalDiv .modal > section > footer .close {
    padding: 5px 11px;
    float: right; /*오른쪽 정렬*/
