    -webkit-animation: modal-bg-show .3s;
            animation: modal-bg-show .3s;
}

.JobPostingModalDiv @keyframes modal-show {
    from {
        opacity: 0;
        margin-top: -50px;
    }
    to {
        opacity: 1;
        margin-top: 0;
    }
}
